@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:300,400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  box-sizing: border-box;
  font-family: "Roboto Slab", serif;
}

*:focus {
  outline: none;
}

textarea {
  resize: none;
}

/***TOP BAR STUFF***/

.topbar {
  position: fixed;
  width: 100%;
  border: 1px solid grey;
  color: white;
  height: 100px;
  background: #7da3d0;
  cursor: default;
  z-index: 4;
}

.topbar .tools {
  position: relative;
  width: 100%;
  top: 45px;
  height: 55px;
  display: inline-block;
}

.topbar .tools button,
.topbar .tools .text-speed-slider {
  float: left;
}

.topbar .tools .new-char-button {
  background: #7dcdd0;
  border: none;
  height: 50px;
  margin-left: 30px;
  text-align: center;
  box-shadow: inset 0 -5px #38979a;
  border-radius: 10px;
  -webkit-user-select: none; /* Chrome/Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+ */
}

.topbar .tools .new-char-button:active {
  height: 45px;
  position: relative;
  padding-top: 6px;
  top: 5px;
  box-shadow: inset 0 0px #38979a;
}

.topbar .tools .new-char-button p {
  position: relative;
  margin-bottom: 5px;
}

.tools-middle {
  margin: auto;
  width: 55px;
  height: 55px;
}
.play-button {
  position: relative;
  width: 50px;
  height: 50px;

  background: #7dd081;
  border: none;
  box-shadow: inset 0 -5px #30b64c;
  border-radius: 10px;
  -webkit-user-select: none; /* Chrome/Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+ */
}
.play-button:active {
  height: 45px;
  padding-top: 6px;
  top: 5px;
  box-shadow: inset 0 0px;
}
.play-icon {
  top: 0;
  width: 25px;
  height: 25px;
  border-style: solid;
  border-width: 12px 0px 12px 25px;
  border-color: transparent transparent transparent #0f542c;
  margin-left: 8px;
  border-radius: 3px;
}

.topbar .title {
  position: absolute;
  width: 30%;
  font-weight: bolder;
  font-size: 32px;
  height: 45px;
  text-align: center;
  text-shadow: -1px 1px black;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}

/***CHOOSE NEXT CHARACTER MENU***/
.choose-next-character {
  position: absolute;
  top: 50px;
  left: 30px;
  height: 416px;
  width: 110px;
  z-index: 2;
  border: 1px solid #7dcdd0;
  border-radius: 5px;
  background: #7dcdd0;
  overflow-y: scroll;
  overflow-x: hidden;
}

.choose-next-character::-webkit-scrollbar {
  width: 6px;
}

.choose-next-character::-webkit-scrollbar-thumb {
  background: rgba(50, 50, 50, 0.75);
  border-radius: 10px;
}

.choose-next-character::-webkit-scrollbar-track {
  border-radius: 10px;
  background: #38979a;
}
.choose-next-character button,
.choose-next-character button img {
  padding: 0;
  margin: 0;
  border: none;
}
.choose-next-character button img {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.choose-next-character button {
  padding: 1px;
  width: 102px;
  height: 102px;
  border: none;
  background: none;
}

/***DIALOG EDITOR STUFF***/

.dialog-editor {
  position: absolute;
  top: 100px;
  background-color: #434343;
  height: 5000px;
  width: 100%;
  overflow-x: hidden;
}

.grid-background {
  background-color: transparent;
  background-image: -webkit-gradient(
      linear,
      left bottom, left top,
      color-stop(24%, transparent),
      color-stop(25%, rgba(255, 255, 255, 0.05)),
      color-stop(26%, rgba(255, 255, 255, 0.05)),
      color-stop(27%, transparent),
      color-stop(74%, transparent),
      color-stop(75%, rgba(255, 255, 255, 0.05)),
      color-stop(76%, rgba(255, 255, 255, 0.05)),
      color-stop(77%, transparent),
      to(transparent)
    ),
    -webkit-gradient(
      linear,
      left top, right top,
      color-stop(24%, transparent),
      color-stop(25%, rgba(255, 255, 255, 0.05)),
      color-stop(26%, rgba(255, 255, 255, 0.05)),
      color-stop(27%, transparent),
      color-stop(74%, transparent),
      color-stop(75%, rgba(255, 255, 255, 0.05)),
      color-stop(76%, rgba(255, 255, 255, 0.05)),
      color-stop(77%, transparent),
      to(transparent)
    );
  background-image: -webkit-linear-gradient(
      bottom,
      transparent 24%,
      rgba(255, 255, 255, 0.05) 25%,
      rgba(255, 255, 255, 0.05) 26%,
      transparent 27%,
      transparent 74%,
      rgba(255, 255, 255, 0.05) 75%,
      rgba(255, 255, 255, 0.05) 76%,
      transparent 77%,
      transparent
    ),
    -webkit-linear-gradient(
      left,
      transparent 24%,
      rgba(255, 255, 255, 0.05) 25%,
      rgba(255, 255, 255, 0.05) 26%,
      transparent 27%,
      transparent 74%,
      rgba(255, 255, 255, 0.05) 75%,
      rgba(255, 255, 255, 0.05) 76%,
      transparent 77%,
      transparent
    );
  background-image: linear-gradient(
      0deg,
      transparent 24%,
      rgba(255, 255, 255, 0.05) 25%,
      rgba(255, 255, 255, 0.05) 26%,
      transparent 27%,
      transparent 74%,
      rgba(255, 255, 255, 0.05) 75%,
      rgba(255, 255, 255, 0.05) 76%,
      transparent 77%,
      transparent
    ),
    linear-gradient(
      90deg,
      transparent 24%,
      rgba(255, 255, 255, 0.05) 25%,
      rgba(255, 255, 255, 0.05) 26%,
      transparent 27%,
      transparent 74%,
      rgba(255, 255, 255, 0.05) 75%,
      rgba(255, 255, 255, 0.05) 76%,
      transparent 77%,
      transparent
    );
  background-size: 50px 50px;
  height: 100%;
  width: 100%;
}

/***DIALOG PIECE STUFF***/

.dialog-piece {
  position: absolute;
  top: 0;

  padding: 6px;

  border-radius: 5px;
  height: 114px;
  width: 414px;
  z-index: 1;
}

.delete-button {
  background: rgba(100, 100, 100, 0.05);
  border-radius: 50%;
  padding: 0;
  color: rgba(211, 211, 211, 0.5);
  font-size: 10px;
  height: 20px;
  width: 20px;
  border: none;
  position: absolute;
  right: 3px;
  top: 3px;
  z-index: 2;
}

.delete-button:hover {
  color: black;
  background: rgba(100, 100, 100, 1);
  cursor: pointer;
}

.dialog-character-pic,
.dialog-character-pic img {
  position: relative;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  float: left;
  height: 100px;
}
.dialog-character-pic {
  width: 100px;
}
.dialog-character-pic img {
  width: 100px;
}

.dialog-content {
  position: relative;
  float: left;
  width: 300px;
  height: 100%;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}
.dialog-content textarea {
  width: 300px;
  height: 100px;
  background: #dddbce;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}

.node-handle,
.node-socket {
  position: absolute;
  left: 50%;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  border-radius: 50%;
  width: 15px;
  height: 15px;
}

.node-socket {
  background: darkgray;
}

.node-handle {
  bottom: -3px;
  background: #7dcdd0;
}

/***dialog tester section***/
.dialog-tester {
  position: absolute;
  top: 100px;
  background-color: #9b9891;
  height: calc(100% - 100px);
  width: 100%;
  z-index: 2;
  -webkit-animation: mover 1s;
          animation: mover 1s;
}

/***Pop up animation***/
@-webkit-keyframes mover {
  0% {
    -webkit-transform: translateY(900px);
            transform: translateY(900px);
  }
}
@keyframes mover {
  0% {
    -webkit-transform: translateY(900px);
            transform: translateY(900px);
  }
}

.dialog-test-here {
  position: absolute;
  top: 0;
  width: 100%;
  height: calc(100% - 82px);
  background: transparent;
  z-index: 3;
  padding: 26px;
  box-shadow: inset 0 0 2px 3px #1e1f36, inset 0 0 4px 8px #322754,
    inset 0 0 4px 9px #e7eaf3, inset 0 0 1px 13px #1e1f36;
}

.dialog-render-area {
  position: absolute;
  top: 13px;
  left: 13px;
  width: calc(100% - 26px);
  height: calc(100% - 26px);
  background: transparent;
  overflow: auto;
}

.continue-arrow {
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.choice-test-render,
.dialog-test-render {
  width: 60%;
  height: 115px;
  padding: 7px;
  border-radius: 10px;
  margin-bottom: 5px;
}
.choice-test-render {
  box-shadow: inset 0 0 2px 1px #1b5f50, inset 0 0 2px 4px #42bd92,
    inset 0 0 1px 5px #bdddc9, inset 0 0 1px 6px #1b5f50;
  border: 1px solid #1b5f50;
  background: #42bd92;
  float: right;
}
.choice-test-render:hover {
  box-shadow: inset 0 0 2px 1px #3e9159, inset 0 0 2px 4px #8be674,
    inset 0 0 1px 5px #dff1e1, inset 0 0 1px 6px #3e9159;
  background: #8be674;
  border: 1px solid #3e9159;
}
.choice-test-render img {
  border: 1px solid #1b5f50;
}
.dialog-test-render {
  box-shadow: inset 0 0 2px 1px #54302c, inset 0 0 2px 4px #b87953,
    inset 0 0 1px 5px #d9cabd, inset 0 0 1px 6px #54302c;
  border: 1px solid #54302c;
  background: #b87953;
  float: left;
}
.dialog-test-render img {
  border: 1px solid #54302c;
}

.choice-test-render img,
.dialog-test-render img {
  border-radius: 3px;
  height: 100px;
  width: 100px;
  position: relative;
  float: left;
}
.choice-test-render h3,
.dialog-test-render h3 {
  position: relative;
  float: left;
  font-size: 24px;
  height: 30px;
}
.choice-test-render p,
.dialog-test-render p {
  position: relative;
  border-radius: 3px;
  margin-top: 2px;
  width: calc(100% - 100px);
  height: calc(100% - 32px);
  background: #dddbce;
  float: left;
  overflow-wrap: break-word;
  overflow: auto;
}

.dialog-tester .start-dialog-button {
  position: absolute;
  height: 50px;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  text-align: center;
  bottom: 100px;
  font-size: 32px;
  background: #7dd081;
  border-radius: 10px;
  box-shadow: inset 0 -5px #30b64c;
  border: none;
}

.dialog-tester .start-dialog-button:active {
  height: 45px;
  box-shadow: inset 0 0;
  padding-top: 5px;
}

.dialog-tester .bottom-bar {
  width: 100%;
  height: 82px;
  position: absolute;
  bottom: 0;
  background: #805344;
  padding: 14px;
  box-shadow: inset 0 0 2px 3px #32232a, inset 0 0 4px 8px #56363b,
    inset 0 0 4px 9px #e7ddd7, inset 0 0 1px 13px #32232a;
}

.bottom-bar button {
  position: relative;
  float: left;
  background: #56363b;
  width: 50px;
  height: 50px;
  border-radius: 10px;
  box-shadow: inset 0 -5px #32232a;
  margin: 2px;
  border: none;
}

.bottom-bar button:active {
  box-shadow: inset 0 0;
  height: 45px;
  top: 5px;
  padding-top: 6px;
}

.bottom-bar .slider {
  position: relative;
  float: left;
  text-align: center;
  margin: 2px;
  padding: 2px;
  border-radius: 10px;
  background: #b2927e;
  border: 2px solid #59413d;
}

